<template>
  <div id="app">
    <Header v-bind:cart="cart" v-on:show-cart="showCart" />
    <Items v-bind:items="items" v-on:add-to-cart="addToCart" />
    <Cart
      v-bind:cart="cart"
      :promotions="promotions"
      v-on:remove-item="removeCartItem"
      v-on:hide-cart="hideCart"
    />
  </div>
</template>

<script>
import Header from "./components/layout/Header";
import Items from "./components/Items";
import Cart from "./components/Cart";
import ItemService from "./services/ItemService";
export default {
  name: "app",
  components: {
    Header,
    Items,
    Cart
  },
  data() {
    return {
      items: [],
      cart: [],
      promotions: [
        {
          promoCode: "20%OFF",
          off: "percent",
          value: 0.2,
          canCombine: false
        },
        {
          promoCode: "5%OFF",
          off: "percent",
          value: 0.05,
          canCombine: true
        },
        {
          promoCode: "20EUROFF",
          off: "amount",
          value: 20,
          canCombine: true
        }
      ],
      comboPrice: [
        {
          itemId: "2",
          minQty: 3,
          itemPrice: 65.0
        },
        {
          itemId: "4",
          minQty: 2,
          newPrice: 35.0
        }
      ],
      appliedPromos: [],
      promoCode: "Promo code",
      promoAllowed: true,
      cantApplyMessage: undefined
    };
  },
  async created() {
    try {
      this.items = await ItemService.getItems();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    showCart() {
      document
        .getElementById("sidebarCart")
        .classList.add("overlay", "visible");
    },
    hideCart() {
      document
        .getElementById("sidebarCart")
        .classList.remove("overlay", "visible");
    },
    removeCartItem(id) {
      // eslint-disable-next-line no-console
      this.cart = this.cart.filter(cartItem => cartItem.itemId !== id);
    },
    addToCart(item) {
      let inCart = false;
      let idToAdd;
      this.cart.forEach(element => {
        if (element.itemId === item.itemId) {
          inCart = true;
          idToAdd = element.itemId;
        }
      });
      if (inCart) {
        this.cart.forEach(element => {
          if (element.itemId === idToAdd) {
            element.itemQty += 1;
            alert(element.itemQty);
          }
        });
      } else {
        this.cart.push(item);
      }
    }
  }
};
</script>

